nav {
  padding: 1.5rem 1rem;
}

@media (min-width: 768px) {
  nav {
    padding: 1.75rem 2rem;
  }
}

@media (min-width: 1280px) {
  nav {
    padding: 2rem 3rem;
  }
}

nav > div {
  text-align: center;
  flex-flow: column;
  align-items: center;
  gap: 2rem;
  display: flex;
}

nav > div .fields {
  width: 100%;
  max-width: 768px;
}

@media (min-width: 1024px) {
  nav > div {
    text-align: left;
    flex-flow: row;
    justify-content: center;
    gap: 4rem;
  }

  nav > div > div:first-child {
    width: 260px;
  }
}

nav p {
  color: #fffc;
  margin-bottom: 1rem;
}

nav form {
  flex-flow: wrap;
  place-items: center;
  display: flex;
}

nav label[for="ano"] {
  max-width: 100px;
  flex: 100px;
}

nav label[for="semestre"] {
  max-width: 150px;
  flex: 150px;
}

nav label[for="busca"] {
  flex: 2 320px;
}

.categories {
  flex-flow: column;
  gap: 3rem;
  padding: 1.5rem 1rem 3rem;
  display: flex;
}

@media (min-width: 768px) {
  .categories {
    padding: 1.75rem 2rem 3.5rem;
  }
}

@media (min-width: 1280px) {
  .categories {
    padding: 2rem 0 4rem;
  }
}

.categories .category {
  width: 100%;
  flex-flow: column;
  gap: 1rem;
  display: flex;
}

.categories .category header {
  flex: 1;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.categories .cards {
  width: 100%;
  grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
  gap: 1rem;
  display: grid;
}

@media (min-width: 768px) {
  .categories .cards {
    grid-template-columns: repeat(auto-fill, minmax(380px, 1fr));
  }
}

.categories .cards .card {
  min-height: 173px;
}

.categories .cards:not(.large) {
  grid-template-columns: repeat(auto-fill, minmax(140px, 1fr));
}

@media (min-width: 768px) {
  .categories .cards:not(.large) {
    grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
  }
}

@media (min-width: 1280px) {
  .categories .cards:not(.large) {
    grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
  }
}

.categories .cards:not(.large) .card {
  min-height: 260px;
}

